import React from 'react';

const BlogDetailImages = ({gallery}) =>
    gallery.map((image, i) => {
        return (
            <div key={`${image.id}`}>
                <figure>
                    <a href={image.file.url}>
                        <img
                            src={image.file.url}
                            data-src={image.file.url}
                            width="300"
                            alt={image.title}
                            data-uk-img
                        />
                    </a>
                </figure>
            </div>
        );
    });

export default BlogDetailImages;
