import React from 'react';
import { graphql } from "gatsby";
import { Helmet } from "react-helmet";
import Layout from "../layouts/Layout";
import * as Markdown from 'react-markdown';
import BlogDetailImages from "../components/Blog/presentation/BlogDetailImages";
import CodeBlock from "../lib/CodeBlock";
import moment from 'moment';
import Contact from '../components/Contact';

const BlogDetail = props => {
    const { date, title, subtitle, mainImage, imageGallery, content } = props.data.contentfulBlog;

    return (
        <Layout>
            <Helmet>
                <title>{title}</title>
                <script async src="//cdn.embedly.com/widgets/platform.js" charset="UTF-8"></script>
            </Helmet>
            <section className="uk-section uk-article">
                <div className="uk-container uk-container-small">
                    <p className="uk-text-bold uk-h2 uk-margin-remove-adjacent uk-margin-remove-top">{title}</p>
                    <p className="uk-article-meta">Posted on {moment(date).format('MMMM D, YYYY')}</p>
                    <p className="uk-text-lead uk-margin-bottom">{subtitle.subtitle}</p>
                </div>
                { mainImage ? (
                    <div className="uk-container uk-section uk-padding-small">
                        <div className="uk-position-relative">
                            <img
                                className="blogMainImage"
                                src={mainImage.file.url}
                                data-src={mainImage.file.url}
                                data-uk-img
                                alt={title} />
                        </div>
                    </div>
                ) : null}
                <div className="post-content uk-container uk-container-small">
                    <Markdown
                        source={content.content}
                        renderers={{ code: CodeBlock }}
                        escapeHtml={false}
                    />
                </div>
                { imageGallery && imageGallery.length > 0 &&
                    <div className="uk-container uk-container-small uk-margin-medium">
                        <div className="uk-grid uk-grid-medium uk-child-width-1-2 uk-child-width-1-3@s"
                            data-uk-grid data-uk-lightbox>
                            <BlogDetailImages gallery={imageGallery} />
                        </div>
                    </div>
                }
            </section>
            <Contact />
        </Layout>
    );
}

export default BlogDetail;
export const blogDetailQuery = graphql`
    query blogDetailQuery($slug: String!) {
        contentfulBlog(slug: {eq: $slug}) {
            imageGallery {
                file {
                    url
                    fileName
                }
                title
                description
            }
            date
            title
            subtitle {
                subtitle
            }
            slug
            mainImage {
                file {
                    url
                    fileName
                }
            }
            content {
                content
            }
        }
    }
`
